.App {
  text-align: center;

}

#Navbar{
  background-color: #7B2CBF;
}

#Navbar li:hover{
  animation: Navbar 1s infinite;
}

@keyframes Navbar {
  100% {
    background-color: #C77DFF;
    border-radius: 5px;
  }
}

.btn{
  color: #7B2CBF;
  border-color: #7B2CBF;
}

#Contacts{
  position:fixed;
  top: 50%;
  left: 16px;
  transform: translate(0% , -50%);
  opacity: .7 ;

}

.Shadow{
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5);
}

